import React, { useState } from "react";
import {
  Form,
  Button,
  Container,
  Row,
  Col,
  Alert,
  Card,
} from "react-bootstrap";
import { useTranslation } from "react-i18next";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const ContactPage = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [contact, setContact] = useState({
    name: "",
    email: "",
    description: "",
  });
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      await axios.post("/api/contacts", contact);
      setLoading(false);
      setError(""); // تنظيف الأخطاء
      navigate("/thankyou"); // التوجيه إلى صفحة الشكر
    } catch (err) {
      setLoading(false);
      setError(
        err.response?.status === 400
          ? t("contact.errorMessage") || "يرجى ملء جميع الحقول المطلوبة"
          : t("contact.serverErrorMessage") || "حدث خطأ أثناء الاتصال بالخادم",
      );
    }
  };

  return (
    <Container className="mt-5">
      <Row className="justify-content-center">
        <Col md={8} lg={6}>
          <Card className="shadow-lg border-0">
            <Card.Body>
              <h2 className="text-center mb-4">
                {t("contact.contactForm.title") || "اتصل بنا"}
              </h2>
              {error && <Alert variant="danger">{error}</Alert>}
              <Form onSubmit={handleSubmit}>
                <Form.Group className="mb-3" controlId="formName">
                  <Form.Label>
                    {t("contact.contactForm.name") || "الاسم"}
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder={t("contact.contactForm.name") || "الاسم"}
                    value={contact.name}
                    onChange={(e) =>
                      setContact({ ...contact, name: e.target.value })
                    }
                    required
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formEmail">
                  <Form.Label>
                    {t("contact.contactForm.email") || "البريد الإلكتروني"}
                  </Form.Label>
                  <Form.Control
                    type="email"
                    placeholder={
                      t("contact.contactForm.email") || "البريد الإلكتروني"
                    }
                    value={contact.email}
                    onChange={(e) =>
                      setContact({ ...contact, email: e.target.value })
                    }
                    required
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formDescription">
                  <Form.Label>
                    {t("contact.contactForm.message") || "الوصف"}
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={4}
                    placeholder={
                      t("contact.contactForm.messageHelp") ||
                      "يرجى ملء جميع الحقول المطلوبة"
                    }
                    value={contact.description}
                    onChange={(e) =>
                      setContact({ ...contact, description: e.target.value })
                    }
                    required
                  />
                </Form.Group>
                <Button
                  variant="primary"
                  type="submit"
                  className="w-100"
                  disabled={loading}
                >
                  {loading
                    ? t("contact.contactForm.sending") || "جاري الإرسال..."
                    : t("contact.contactForm.submit") || "إرسال"}
                </Button>
              </Form>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default ContactPage;
